import {validateEmbed, validateIsEmbed} from "./validatorUtils";

/**
 *
 */
export function validateEvaluationForm() {
    document.getElementById('udeh-form').addEventListener("submit", function(e) {
        e.preventDefault();
        validateIsEmbed();
        let validEmbed = validateEmbed('id_evaluation_introduction_embed',
            'udeh-form',
            'fitem_id_evaluation_introduction_embed');
        if (!validEmbed) {
            return;
        }
        window.$('#udeh-form').submit();
    });
}